.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: -20%;
    background: var(--title-color);
    opacity: 0.8;
    padding: 0.3rem 0.5rem;
    border-radius: 0.4rem;
    z-index: 1000;
    transition: .4s;
}
.scrollup__icon {
    font-size: 1.5rem;
    color: var(--container-color);
}
.scrollup:hover {
    background: var(--title-color-dark);
}

/* show scroll  */

.show-scroll {
    bottom: 3rem;
}

@media screen and (max-width: 992px) {
    .scrollup {
        right: 1.5rem;
        padding: 0.25rem 0.4rem;
    }
    .scrollup__icon {
        font-size: 1.25rem;
    }
}
@media screen and (max-width: 768px) {
    .show-scroll {
        bottom: 2rem;
    }
}
@media screen and (max-width: 350px) {
    .scrollup {
        right: 1rem;
    }
}